import {Link} from "gatsby"
import {DiOpensource} from "react-icons/di"
import {LuFileJson} from "react-icons/lu"
import React, {FunctionComponent} from "react"
import {Moment} from "moment"
import {PiPath, PiSmileyMeltingFill} from "react-icons/pi";
import { AiOutlineDotNet, AiOutlineFileUnknown } from "react-icons/ai";
import _ from "lodash";
import {FaTimeline} from "react-icons/fa6";
import {GrServers, GrServices} from "react-icons/gr";

interface PostPreviewProps {
    link: string
    title: string
    date: Moment
    tags: string[]
}

export const PostPreview: FunctionComponent<PostPreviewProps> = ({
    link,
    title,
    date,
    tags,
}) => {
    return <Link
        to={link}
        className="
            flex
            flex-col
            gap-5
            bg-white
            border
            rounded-2xl
            shadow
            p-5
            scale
            transition-all
            hover:scale-105
        "
    >
        <div
            id="post-preview"
            className="flex flex-col gap-2"
        >
            <h4>{title}</h4>
            <h5 className="text-neutral-500">{date.fromNow()}</h5>
        </div>
        <div className="grid grid-cols-2">
            <ul className="col-span-1 flex flex-row gap-2">
                { _.orderBy(tags).map((tag) => <PostTag tag={tag} />) }
            </ul>
            <div className="col-span-1 flex flex-row justify-end text-right items-center">
                <LuFileJson/>
            </div>
        </div>
    </Link>
}

interface PostTagProps {
    tag: string
}

const PostTag: FunctionComponent<PostTagProps> = ({tag}) => {

    return <li className="bg-secondary rounded pl-3 pr-3 pt-1 pb-1 flex flex-row gap-1 items-center cursor-default">
        <PostTagIcon tag={tag} />
        <span>{postTagLabel(tag)}</span>
    </li>
}

interface PostTagIconProps {
    tag: string
}

// see: https://react-icons.github.io/react-icons/search
const PostTagIcon: FunctionComponent<PostTagIconProps> = ({tag}) => {
    switch (tag) {
        case "open-source":
            return <DiOpensource/>
        case "blog-meta":
            return <PiSmileyMeltingFill />
        case "aspnet":
            return <AiOutlineDotNet />
        case "dotnet":
            return <AiOutlineDotNet />
        case "process":
            return <PiPath />
        case "agile":
            return <FaTimeline />
        case "microservices":
            return <GrServices />
        case "infrastructure":
            return <GrServers />
        default:
            return <AiOutlineFileUnknown />
    }
}

function postTagLabel(tag: string) {
    switch (tag) {
        case "open-source":
            return "Open Source"
        case "blog-meta":
            return "Blog Meta"
        case "asp-net":
            return "ASP.NET"
        case "dotnet":
            return "Dotnet"
        case "process":
            return "Process"
        case "agile":
            return "Agile"
        case "microservices":
            return "Microservices"
        case "infrastructure":
            return "Infrastructure"
        default:
            return _.startCase(tag)
    }
}