import React, {FunctionComponent} from "react"
import {graphql, HeadFC, PageProps, useStaticQuery} from "gatsby"
import {MainLayout} from "../layouts/MainLayout"
import _ from "lodash";
import {subheadings} from "../subheadings"
import moment from "moment";
import {PostPreview} from "../components/PostPreview";


const query = graphql`
    {
        package {
            version
        }
        site {
            siteMetadata {
                title
            }
        }
        allMdx(sort: { frontmatter: { posted: DESC }}) {
            totalCount

            nodes {
                fields {
                    slug
                }
                frontmatter {
                    title
                    posted
                    tags
                }
                excerpt
                tableOfContents
            }
        }
    }
`

const IndexPage: FunctionComponent<PageProps> = () => {

    const indexData = useStaticQuery(query)

    return <MainLayout
        title={indexData.site.siteMetadata.title}
        showTitle={true}
        pageTitle={_.shuffle(subheadings)[0]}
        className="flex flex-col gap-7"
        version={indexData.package.version}
    >
        { indexData.allMdx.nodes.map((node) => <PostPreview
            link={node.fields.slug}
            title={node.frontmatter.title}
            date={moment(node.frontmatter.posted)}
            tags={node.frontmatter.tags}
        />) }
    </MainLayout>
}

export default IndexPage

export const Head: HeadFC = () =>  {

    const indexData = useStaticQuery(query)

    return <>
        <title>{indexData.site.siteMetadata.title}</title>
        <link rel="alternate" type="application/rss+xml" title="RSS Feed" href="/feed.xml"/>
    </>
}
